import Swiper from "swiper";

document.addEventListener("DOMContentLoaded", function () {
	const addTabsSwiper = (parentClass) => {
		if (window.innerWidth <= 1024) {
			const tabContents = document.querySelectorAll(
				`${parentClass} .tabs .tabs__tab-content`
			);

			tabContents.forEach((tabContent, i) => {
				tabContent.classList.add(`swiper-${i}`);
				const swiperBlock = document.createElement("div");
				const cols = tabContent.querySelectorAll(".tabs__col");

				swiperBlock.className = "swiper-wrapper";
				tabContent.insertBefore(swiperBlock, tabContent.querySelector(".tabs__btns"));
				cols.forEach((col) => {
					col.classList.add("swiper-slide");
					swiperBlock.append(col);
				});

				const swiper = new Swiper(`${parentClass} .swiper-${i}`, {
					slidesPerView: 1,
					breakpoints: {
						600: {
							slidesPerView: 2,
						},
					},
				});
				tabContent.querySelectorAll(".tabs__btn").forEach((btn) => {
					btn.addEventListener("click", () => {
						if (btn.classList.contains("tabs__btn--next")) {
							swiper.slideNext();
						}
						if (btn.classList.contains("tabs__btn--prev")) {
							swiper.slidePrev();
						}
					});
				});
			});
		}
	};

	addTabsSwiper(".header");
	addTabsSwiper(".practices");
});
