document.addEventListener("DOMContentLoaded", function () {
	const tabsBlocks = document.querySelectorAll(".tabs");

	tabsBlocks.forEach((tabsBlock) => {
		const tabs = tabsBlock.querySelectorAll(".tabs__tab");
		const tabContents = tabsBlock.querySelectorAll(".tabs__tab-content");

		tabs.forEach((tab) => {
			tab.classList.remove("tabs__tab--active");
		});
		tabContents.forEach((tabContent) => {
			tabContent.classList.remove("tabs__tab-content--active");
		});
		tabs[0].classList.add("tabs__tab--active");
		tabContents[0].classList.add("tabs__tab-content--active");

		tabs.forEach((tab, i) => {
			tab.addEventListener("click", function (event) {
				event.preventDefault();
				tabs.forEach((tab) => {
					tab.classList.remove("tabs__tab--active");
				});
				tab.classList.add("tabs__tab--active");
				tabContents.forEach((tabContent) => {
					tabContent.classList.remove("tabs__tab-content--active");
				});
				tabContents[i].classList.add("tabs__tab-content--active");
			});
		});
	});
});
