document.addEventListener("DOMContentLoaded", function () {
	// template/src/blocks/modules/header/header.js
	const header = document.querySelector(".header");
	const menuList = header.querySelector(".header__menu .menu__list");
	const parentItem = menuList.querySelectorAll(".menu__item--parent");

	parentItem.forEach((item) => {
		const currentSubmenu = item.querySelector(".menu__submenu");
		const hideSubMenus = () => {
			parentItem.forEach((item) => {
				const currentSubmenu = item.querySelector(".menu__submenu");

				currentSubmenu.classList.remove("menu__submenu--active");
			});
		};

		item.addEventListener("mouseover", function () {
			hideSubMenus();
			currentSubmenu.classList.add("menu__submenu--active");
		});

		currentSubmenu.addEventListener("mouseout", function () {
			hideSubMenus();
		});
	});

	//open mobile menu

	const mobileMenuButton = header.querySelector(".header__btn--mobile");
	const mobileMenu = document.querySelector(".menu-mobile");

	mobileMenuButton.addEventListener("click", toggleMobileMenu);

	function toggleMobileMenu() {
		mobileMenu.classList.toggle("menu-mobile--active");
		document.body.style.overflow = mobileMenu.classList.contains("menu-mobile--active") ? "hidden" : "auto";
		updateMobileMenuIcon();
	}

	function updateMobileMenuIcon() {
		const iconUseElement = mobileMenuButton.querySelector("svg use");
		const iconId = mobileMenu.classList.contains("menu-mobile--active") ? "#burger-close" : "#burger-open";
		iconUseElement.setAttribute("xlink:href", `img/sprites/sprite.svg${iconId}`);
	}
});
