import Swiper from "swiper";
import { Navigation } from "swiper/modules";
document.addEventListener("DOMContentLoaded", () => {
	const swiper = new Swiper(".posts-swiper", {
		// Optional parameters
		loop: true,
		slidesPerView: 1,
		breakpoints: {
			1024: {
				slidesPerView: 3,
			},
			600: {
				slidesPerView: 2,
			}
		},
		spaceBetween: 100,
		// Navigation arrows
		navigation: {
			nextEl: ".block-posts__btn--next",
			prevEl: ".block-posts__btn--prev",
		},
		modules: [Navigation],
	});

	document.querySelectorAll(".posts-swiper__btn").forEach((btn) => {
		btn.addEventListener("click", () => {
			if (btn.classList.contains("posts-swiper__btn--next")) {
				swiper.slideNext();
			}
			if (btn.classList.contains("posts-swiper__btn--prev")) {
				swiper.slidePrev();
			}
		});
	});
});
