import Swiper from "swiper";
import { Navigation } from "swiper/modules";
document.addEventListener("DOMContentLoaded", () => {
	const swiper = new Swiper(".testimonials__wrapper", {
		// Optional parameters
		loop: true,
		slidesPerView: 1,
		breakpoints: {
			768: {
				slidesPerView: 2,
			},
		},
		
		history: {
			enabled: true,
			key: "slide",
			root: "localhost:4000",
		},
		// Navigation arrows
		navigation: {
			nextEl: ".testimonials__btn--next",
			prevEl: ".testimonials__btn--prev",
		},
		modules: [Navigation],
	});

	document.querySelectorAll(".testimonials__btn").forEach((btn) => {
		btn.addEventListener("click", () => {
			const leftIndex = swiper.realIndex;
			let rightIndex;
			if (swiper.realIndex + 1 > swiper.slides.length - 1) {
				rightIndex = 0;
			} else {
				rightIndex = swiper.realIndex + 1;
			}
			const changeClasses = () => {
				swiper.slides.forEach((slide) => {
					slide.classList.remove(
						"testimonials__testimonial--left",
						"testimonials__testimonial--right"
					);
				});
				swiper.el
					.querySelector(`[data-swiper-slide-index="${leftIndex}"]`)
					.classList.add("testimonials__testimonial--left");
				swiper.el
					.querySelector(`[data-swiper-slide-index="${rightIndex}"]`)
					.classList.add("testimonials__testimonial--right");
			};
			if (btn.classList.contains("testimonials__btn--next")) {
				swiper.slideNext();
			}
			if (btn.classList.contains("testimonials__btn--prev")) {
				swiper.slidePrev();
			}
			changeClasses()
		});
	});
});

