import Swiper from "swiper";

document.addEventListener("DOMContentLoaded", function () {
	if (window.innerWidth <= 768) {
		const projectsGrids = document.querySelectorAll(".projects-grid");

		projectsGrids.forEach((projectsGrid, i) => {
			projectsGrid.classList.add(`swiper-${i}`);
			projectsGrid.querySelector(".projects-grid__wrapper").classList.add("swiper-wrapper");
			projectsGrid.querySelectorAll(".projects-grid__project").forEach((project) => {
				project.classList.add("projects-grid__project--mobile", "swiper-slide");
			});

			const swiper = new Swiper(`.projects-grid.swiper-${i}`, {});
			projectsGrid.querySelectorAll(".projects-grid__btn").forEach((btn) => {
				btn.addEventListener("click", () => {
					if (btn.classList.contains("projects-grid__btn--next")) {
						swiper.slideNext();
					}
					if (btn.classList.contains("projects-grid__btn--prev")) {
						swiper.slidePrev();
					}
				});
			});
		});
	}
});
